import React, { Component } from 'react';
import PropTypes from 'prop-types';


function debounce(func, wait, immediate) {
    let timeout;

    return function () {
        const context = this, args = arguments;
        const later = () => {
            timeout = null;
            if (!immediate) func.apply(context, args);
        };
        const callNow = immediate && !timeout;

        clearTimeout(timeout);
        timeout = setTimeout(later, wait);
        if (callNow) func.apply(context, args);
    };
}


export default class SelectableText extends Component {

    mouseEvent() {

        let text = '';

        if (window.getSelection) {
            text = window.getSelection().toString();
        }

        if (!text || !text.length) {
            return false;
        }

        this.props.setProps({
            selected_text: text,
            selection_start: window.getSelection().anchorOffset,
            selection_end: window.getSelection().focusOffset,
            rangetext: window.getSelection().getRangeAt(0).toString(),
            startoffset: window.getSelection().getRangeAt(0).startoffset,
            endoffset: window.getSelection().getRangeAt(0).endOffset,
        })
    }

    onMouseUp(event) {
        debounce(() => {
            if (this.doucleckicked) {
                this.doucleckicked = false;
                this.dismissMouseUp++;
            } else if (this.dismissMouseUp > 0) {
                this.dismissMouseUp--;
            } else {
                this.mouseEvent.bind(this)();
            }
        }, 200).bind(this)();
    }

    onDoubleClick(event) {
        event.stopPropagation();

        this.doucleckicked = true;
        this.mouseEvent.bind(this)();
    }

    render() {
        return (
            <div id={this.props.id}
                style={{
                    whiteSpace: 'pre-wrap',
                    display: 'inline-block',
                    verticalAlign: 'text-top',
                    margin: '10px',
                    width: this.props.width
                }}
                onMouseUp={this.onMouseUp.bind(this)}
                onDoubleClick={this.onDoubleClick.bind(this)}>
                {this.props.text}
            </div>
        );
    }
}

SelectableText.defaultProps = {};

SelectableText.propTypes = {
    id: PropTypes.string,
    width: PropTypes.string,
    text: PropTypes.element.isRequired,
    selected_text: PropTypes.string,
    selection_start: PropTypes.number,
    selection_end: PropTypes.number,
    rangetext: PropTypes.string,
    // startoffset: PropTypes.number,
    // endoffset: PropTypes.number,
    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func
};